import { getCurrentInstance, ref, watch } from "vue";
import { useElApi } from "../../hooks";
import { dataset } from "../../utils";
// 操作
export function useAction({ config, form, Form }) {
    // 设置数据
    function set({ prop, key, path }, data) {
        let p = path || "";
        if (path) {
            dataset(config, p, data);
        }
        else {
            let d;
            if (prop) {
                function deep(arr) {
                    arr.forEach((e) => {
                        if (e.prop == prop) {
                            d = e;
                        }
                        else {
                            if (e.children) {
                                deep(e.children);
                            }
                        }
                    });
                }
                deep(config.items);
            }
            if (d) {
                switch (key) {
                    case "options":
                        d.component.options = data;
                        break;
                    case "props":
                        Object.assign(d.component.props, data);
                        break;
                    case "hidden":
                        d.hidden = data;
                        break;
                    case "hidden-toggle":
                        d.hidden = data === undefined ? !d.hidden : !data;
                        break;
                    default:
                        Object.assign(d, data);
                        break;
                }
            }
            else {
                console.error(`prop "${prop}" not found`);
            }
        }
    }
    // 获取表单值
    function getForm(prop) {
        return prop ? form[prop] : form;
    }
    // 设置表单值
    function setForm(prop, value) {
        form[prop] = value;
    }
    // 设置配置
    function setConfig(path, value) {
        set({ path }, value);
    }
    // 设置数据
    function setData(prop, value) {
        set({ prop }, value);
    }
    // 设置表单项的下拉数据列表
    function setOptions(prop, value) {
        set({ prop, key: "options" }, value);
    }
    // 设置表单项的组件参数
    function setProps(prop, value) {
        set({ prop, key: "props" }, value);
    }
    // 切换表单项的显示、隐藏
    function toggleItem(prop, value) {
        set({ prop, key: "hidden-toggle" }, value);
    }
    // 对部分表单项隐藏
    function hideItem(...props) {
        props.forEach((prop) => {
            set({ prop, key: "hidden" }, true);
        });
    }
    // 对部分表单项显示
    function showItem(...props) {
        props.forEach((prop) => {
            set({ prop, key: "hidden" }, false);
        });
    }
    // 设置标题
    function setTitle(value) {
        config.title = value;
    }
    // 是否展开表单项
    function collapseItem(e) {
        Form.value?.clearValidate(e.prop);
        e.collapse = !e.collapse;
    }
    return {
        getForm,
        setForm,
        setData,
        setConfig,
        setOptions,
        setProps,
        toggleItem,
        hideItem,
        showItem,
        setTitle,
        collapseItem
    };
}
// 选项卡
export function useTabs({ config, Form }) {
    // 选中
    const active = ref();
    // 获取参数
    function get() {
        return config.items.find((e) => e.type === "tabs");
    }
    function set(data) {
        active.value = data;
    }
    function clear() {
        active.value = null;
    }
    // 切换
    function change(value, isValid = true) {
        return new Promise((resolve, reject) => {
            function next() {
                active.value = value;
                resolve();
            }
            if (isValid) {
                let isError = false;
                const arr = config.items
                    .filter((e) => e.group == active.value && !e._hidden && e.prop)
                    .map((e) => {
                    return new Promise((r) => {
                        // 验证表单
                        Form.value.validateField(e.prop, (valid) => {
                            if (valid) {
                                isError = true;
                            }
                            r(valid);
                        });
                    });
                });
                Promise.all(arr).then((msg) => {
                    if (isError) {
                        reject(msg.filter(Boolean));
                    }
                    else {
                        next();
                    }
                });
            }
            else {
                next();
            }
        });
    }
    // 合并
    function mergeProp(item) {
        const d = get();
        if (d && d.props) {
            const { mergeProp, labels = [] } = d.props;
            if (mergeProp) {
                const t = labels.find((e) => e.value == item.group);
                if (t && t.name) {
                    item.prop = `${t.name}-${item.prop}`;
                }
            }
        }
    }
    return {
        active,
        get,
        set,
        change,
        clear,
        mergeProp
    };
}
// 方法
export function useApi({ Form }) {
    return useElApi([
        "open",
        "close",
        "clear",
        "reset",
        "submit",
        "bindForm",
        "changeTab",
        "setTitle",
        "showLoading",
        "hideLoading",
        "collapseItem",
        "getForm",
        "setForm",
        "setData",
        "setConfig",
        "setOptions",
        "setProps",
        "toggleItem",
        "hideItem",
        "showItem",
        "validate",
        "validateField",
        "resetFields",
        "scrollToField",
        "clearValidate"
    ], Form);
}
// 插件
export function usePlugins({ visible }) {
    const that = getCurrentInstance();
    // 事件
    const ev = {
        onOpen: [],
        onClose: [],
        onSubmit: []
    };
    // 监听器
    let timer = null;
    // 插件创建
    function create(plugins) {
        for (const i in ev) {
            ev[i] = [];
        }
        if (timer) {
            timer();
        }
        if (plugins) {
            plugins.forEach((p) => {
                p({
                    exposed: that.exposed,
                    onOpen(cb) {
                        ev.onOpen.push(cb);
                    },
                    onClose(cb) {
                        ev.onClose.push(cb);
                    },
                    onSubmit(cb) {
                        ev.onSubmit.push(cb);
                    }
                });
            });
            timer = watch(visible, (val) => {
                if (val) {
                    setTimeout(() => {
                        ev.onOpen.forEach((e) => e());
                    }, 10);
                }
                else {
                    ev.onClose.forEach((e) => e());
                }
            }, {
                immediate: true
            });
        }
    }
    // 表单提交
    async function submit(data) {
        let d = data;
        for (let i = 0; i < ev.onSubmit.length; i++) {
            const d2 = await ev.onSubmit[i](d);
            if (d2) {
                d = d2;
            }
        }
        return d;
    }
    return {
        create,
        submit
    };
}
